import React, {Component} from "react";
import {Menu} from "semantic-ui-react";
import classes from "../../../static/css/Navbar.css";
import {Link as ScrollLink} from "react-scroll";

export default class  extends Component {
  state = {
    activeItem: "home"
  };
  
  renderMenuItem = (itemName, itemTag, activeItem) => {
    return (<Menu.Item
      name={itemName}
      active={activeItem === itemName}
    >
      <ScrollLink
        to={itemName}
        spy={true}
        smooth={true}
        duration={700}
        id="sclink"
      >
        <b>{itemTag}</b>
      </ScrollLink>
    </Menu.Item>);
  };
  
  render() {
    const activeItem = "home";
    return (
      <div className={classes.navbar}>
        <Menu borderless fixed="top">
          <Menu.Item
            name="home"
            active={activeItem === "home"}
          >
            <ScrollLink
              to="home"
              spy={true}
              smooth={true}
              duration={700}
              id="sclink"
            >
              <b>ferrets.io</b>
            </ScrollLink>
          </Menu.Item>
          <Menu.Menu position="right">
            <Menu.Item
              name="what"
              active={activeItem === "what"}
            >
              <ScrollLink
                to="what"
                spy={true}
                smooth={true}
                duration={700}
                id="sclink"
              >
                <b>What</b>
              </ScrollLink>
            </Menu.Item>
            {this.renderMenuItem("what", "What", activeItem)}
            {this.renderMenuItem("why", "Why", activeItem)}
            {this.renderMenuItem("when", "When", activeItem)}
            {this.renderMenuItem("who", "Who", activeItem)}
  
            <Menu.Item
              name="When"
              active={activeItem === "When"}
            >
              <ScrollLink
                to="when"
                spy={true}
                smooth={true}
                duration={700}
                id="sclink"
              >
                <b>When</b>
              </ScrollLink>
            </Menu.Item>
            <Menu.Item
              name="Who"
              active={activeItem === "Who"}
            >
              <ScrollLink
                to="who"
                spy={true}
                smooth={true}
                duration={700}
                id="sclink"
              >
                <b>Who</b>
              </ScrollLink>
            </Menu.Item>
          </Menu.Menu>
        </Menu>
      </div>
    );
  }
}

